@import url(https://fonts.googleapis.com/css?family=Merriweather+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    box-sizing: border-box;
  font-family: "Merriweather Sans", sans-serif;
}

.App {
    position: relative;
    width: 375px;
    height: 812px;
}

.view {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: -webkit-gradient(linear, left bottom, left top, color-stop(25%, rgba(255, 255, 255, 0.9)), color-stop(40%, rgba(255, 255, 255, 0))), url(/f2e-2019-3/static/media/8C9Evy.90c18347.png) center bottom no-repeat;
    background: -webkit-linear-gradient(bottom, rgba(255, 255, 255, 0.9) 25%, rgba(255, 255, 255, 0) 40%), url(/f2e-2019-3/static/media/8C9Evy.90c18347.png) center bottom no-repeat;
    background: linear-gradient(to top, rgba(255, 255, 255, 0.9) 25%, rgba(255, 255, 255, 0) 40%), url(/f2e-2019-3/static/media/8C9Evy.90c18347.png) center bottom no-repeat;
    z-index: -1;
}

.top-part {
    width: 100%;
    height: 66%;
    padding: 76px 0 13px 0;
}

.window {
    width: 327px;
    height: 447px;
    margin: 0 auto;
    border: 10px solid white;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16)
}

.bottom-part {
    width: 100%;
    height: 34%;
    padding: 42px 0 52px 0;
}

.song-name {
    height: 28px;
    text-align: center;
    font-size: 22px;
    font-weight: bold;
}

.artist {
    height: 20px;
    font-size: 16px;
    text-align: center;
    margin-top: 3px;
}

.progress {
    width: 315px;
    height: 18px;
    margin: 12px auto 8px auto;
    padding: 8px 0 8px 0;
}

.total {
    width: 100%;
    height: 2px;
    background: #DEDEDE;
    margin: 0 auto;
    position: relative;
}

.past-bar {
    height: 100%;
    background: #413D3D;
}

.past-point {
    width: 6px;
    height: 6px;
    background: #413D3D;
    border-radius: 50%;
    position: absolute;
    top: calc(50% - 3px);
}

.time {
    width: 315px;
    height: 13px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    font-size: 10px;
    color: #AEAEAE;
}

.left {

}

.right {

}


.control {
    height: 70px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
}

.control img:not(:first-child) {
    padding-left: 14px;
}
